import { useHttp } from "../../hooks/http.hook";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import {
  heroesFetching,
  heroesFetched,
  heroesFetchingError,
} from "../../actions";
import HeroesListItem from "../heroesListItem/HeroesListItem";
import Spinner from "../spinner/Spinner";

// Задача для этого компонента:
// При клике на "крестик" идет удаление персонажа из общего состояния
// Усложненная задача:
// Удаление идет и с json файла при помощи метода DELETE

const HeroesList = () => {
  const { heroes, heroesLoadingStatus, filters } = useSelector(
    (state) => state
  );

  const dispatch = useDispatch();
  const { request } = useHttp();

  //   useEffect(() => {
  //     dispatch(heroesFetching());
  //     request("https://api.jsonserve.com/IlqUbC/heroes")
  //       .then((data) => {
  //         dispatch(heroesFetched(data));
  //       })
  //       .catch(() => dispatch(heroesFetchingError()));
  //   }, [dispatch, request]);

  if (heroesLoadingStatus === "loading") {
    return <Spinner />;
  } else if (heroesLoadingStatus === "error") {
    return <h5 className="text-center mt-5">Ошибка загрузки</h5>;
  }

  const renderHeroesList = (arr) => {
    if (arr.length === 0) {
      return <h5 className="text-center mt-5">Героев пока нет</h5>;
    }

    return arr.map(({ id, ...props }) => {
      return <HeroesListItem key={id} id={id} heroes={heroes} {...props} />;
    });
  };

  let elements;

  if (filters.type === "All") {
    elements = renderHeroesList(heroes);
  } else {
    elements = renderHeroesList(filters["arr"]);
  }

  return <ul>{elements}</ul>;
};

export default HeroesList;
